import Component from 'inferno-component'
import AppBar from 'components/AppBar'

class App extends Component {
    constructor(props) {
        super(props)
    }

    render() {
        return (
            <div>
                <AppBar />
                <br/>
                <div className="container">
                    {this.props.children}
                </div>
            </div>
        )
    }
}

export default App



