import Component from 'inferno-component'
import history from 'util/history'

class SimpleForm extends Component {

    constructor(props) {
        super(props)
        this.state = { name: '' }
    }

    onNameChange = (e) => {
        this.setState({ name: e.target.value })
    }

    onNextHandler = (e) => {
        e.preventDefault()
        if (this.state.name === '') return
        history.push(`/resume/${this.state.name}`, { name: this.state.name })
    }

    render() {
        return (
            <div className="row">
                <div className="col-xs-3">&nbsp;</div>
                <div className="col-xs-6">
                    <div className="card card-block">
                        <h4 className="card-title text-xs-center">Welcome!</h4>
                        <br/>
                        <div className="md-form">
                            <i className="fa fa-user prefix"></i>
                            <input type="text" className="form-control" 
                                value={this.state.name} onInput={this.onNameChange} 
                                maxLength="30" />
                            <label>Your Name</label>
                        </div>

                        <div className="text-xs-center">
                            <button className="btn btn-primary" onClick={this.onNextHandler}>Next</button>
                        </div>
                    </div>
                </div>
                <div className="col-xs-3">&nbsp;</div>
            </div>
        )
    }
}

export default SimpleForm



